:root{
  --bc: #efeeee;
  --white: #ffffff;
  --black: #000000;
  --dark-blue: #1f2029;
  --extra-dark-blue: #13141a;
  --dark-light: #424455;
  --red: #da2c4d;
  --dark-red: #6e1727;
  --yellow: #f8ab37;
  --grey: #ecedf3;
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --orange: #fd7e14;
  --green: #28a745;
  --light-green: #24e33a;
  --teal: #20c997;
  --cyan: #17a2b8;
  --gray: #6c757d;
  --primary: #d1a9de;
  --secondary: #7015b3;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light-grey: #dbdbdb;
  --dark-grey: #a5a7aa;
  --background-color: #fafafa;
  --red-light: #ed4956;
  --popupperfil-item-hover: #fafafa;
  --siide-content-grey-color: #8e8e8e; 
  --blue-link: #0095f6;

  --font-family-sans-serif: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;

  /*=== Margenes  ===*/
  --mb-1: .5rem;
  --mb-1-2: .8rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;

  /*=== Z index ===*/
  --z-index: 100;

  /*=== Font and typography */
  --big-font-size: 1.25rem;
  --bigger-font-size: 1.5rem;
  --biggest-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 1.4rem;
  --smaller-font-size: .813rem;
  --nav-font-size-icon: 2.5rem;

  /*=== Padding ===*/
  --p-1: .5rem; 
  --p-2: 1rem;

}

@media screen and (min-width: 768px) {
  :root{
    --big-font-size: 1.5rem;
    --bigger-font-size: 2rem;
    --biggest-font-size: 3rem; 
    --normal-font-size: 1.4rem; 
    --smaller-font-size: .875rem;
  }
}

*, ::after, ::before{
  box-sizing: border-box;
}

h1, h2, h3, p, ul{
  margin: 0;
}

ul
{
  padding: 0;
  list-style-type: none;
}

a{text-decoration: none;}

html{
  font-size: 65.5%;
  -webkit-transition: all .5s ease-in;
  transition: all .5s ease-in;
}

body, html 
{
  margin: 0;
  font-family: var(--font-family-sans-serif);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  /*background-color: var(--light-mode-brig);*/
  background-color: var(--background-color);

}

img{
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',monospace;
}

/*=== LAYOUT ===*/

.bd-grid{
  max-width: 1024px;
  /*min-width: 605px;*/
  display: grid;
  grid-template-columns: 100%;
  column-gap: 2rem;
  /*width: calc(100% - 2rem);*/
  margin-left: var(--mb-2);
  margin-right: var(--mb-2);

}

.l-header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-index);
  background-color: var(--white);
  border-bottom: .1rem solid var(--light-grey);

}

.container{
  overflow-x: hidden;
  overflow-y: scroll;
  height: 100vh;
}

/*=== NAV ===*/
.nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.5rem; 
  background-color: var(--white);
  width: 100%;
}

.nav-left, .nav-middle, .nav-right{
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo{
  display: flex;
  align-items: center;
  justify-content: center;
  object-fit: cover;
}


.nav-right .nav-link{
  color: var(--black);
  font-size: var(--normal-font-size);
}

.nav-right .perfil__container{
  position: relative;
  width: var(--nav-font-size-icon);
  height: var( --nav-font-size-icon);
  cursor: pointer;
}

.nav-right .perfil__container .toggle-perfil{
  width: var(--nav-font-size-icon);
  height: var( --nav-font-size-icon);
  border-radius: 50%;
  
}

.nav-right i{
  font-size: var(--nav-font-size-icon);
}

.nav-right .toggle-perfil img{
  border-radius: 50%;
}

.nav-right .toggle-perfil img.active{
  border: .1rem solid var(--black);
  padding: .2rem;
}

.nav-right .nav-link{
  margin: 0 var(--mb-1-2);
}

.nav .input-container{
  position: relative;
  display: none;
}

.nav .input-container .search-input{
  position: absolute;
  top: .6rem;
  left: calc(10rem/2);
  font-size: 1.5rem;
  color: var(--dark-grey);
}

.nav .input-container input[type="text"]{
  width: 20rem;
  border: .1rem solid var(--light-grey); 
  padding: var(--p-1);
  text-align: center;
  border-radius: .5rem;
  font-size: var(--normal-font-size);
  outline: none;
  background-color: var(--background-color);
}

.nav .input-container input[type="text"]:focus {
  text-align: left;
  padding: .5rem .5rem .5rem 4rem;
}

.nav .input-container input[type="text"]:focus ~  .search-input{
  left: 1rem;
}

.messenger-link {
  position: relative;
}

.messenger-notification-number{
  position: absolute;
  height: 1.7rem;
  width: 1.7rem;
  color: var(--white);
  background-color: var(--red-light);
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 1.5rem;
  left: 1.2rem;
  border-radius: 50%;

}
/*=== PopUpPerfil ===*/
.popupperfil__container{
  position: absolute;
  background-color: var(--white);
  width: 20rem;
  height: 16rem;
  right: -1.6rem;
  top: 3.6rem;
  box-shadow: 0 0 .5rem rgba(0,0,0,.2);
  border-radius: .3rem;
  z-index: 150;
}

/* .pointer{
  position: absolute;
  width: .5rem;
  height: .5rem;
  top: -.3rem;
  top: 0;
  right: 2rem;
  right: 10rem;
  top: 5rem;
  border-color: var(--white) transparent transparent var(--white);
  border-width: 1rem;
  border-style: solid;
  box-shadow:  0 0 .5rem rgba(0,0,0,.2);
  transform: rotate(45deg);
  z-index: -1;
} */
 
.popupperfil__container::before{
  content: "";
  position: absolute;
  width: .1rem;
  height: .1rem;
  top: -.4rem;
  right: 2rem;
  border-color: var(--white) transparent transparent var(--white);
  border-width: .5rem;
  border-style: solid;
  box-shadow:  -.3rem -.3rem .5rem -.1rem rgba(0,0,0,.1);
  transform: rotate(45deg);
} 

.popupperfil__container ul{
  display: flex;
  flex-direction: column;
}

.popupperfil__container ul li{
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  padding: 0;
  /*transition: all .5s ease-in;*/
  padding: .5rem 1rem;
}

.popupperfil__container ul li a{
  color: var(--black);
  width: 100%;
}

.popupperfil__container ul li:hover, .popupperfil__footer:hover{
  background-color: var(--popupperfil-item-hover);
}


.popupperfil__container ul li i{
  font-size: 2rem;
  margin-right: .7rem;
}

.popupperfil__footer{
  border-top: .1rem solid var(--light-grey);
  width: 20rem;
  padding: 1rem 1rem;
  font-size: 1.2rem;
}

/*=== Content ===*/
.content{
  margin: 8rem 0 0 0;
  /*display: grid;
  grid-template-columns: 70% 30%;*/
  display: flex;
  position: relative;
  height: 100%;
  width: 100%;
  max-width: 1024px;

}


.content-left{
  width: 100%;
}

/* ===  Stories Container === */
.stories_container{
  width: 100%;
  height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--white);
  border: .1rem solid var(--light-grey);
  border-radius: .3rem;
  overflow: hidden;
  padding: 1rem;

}

.stories-container-inner{
  width: 100%;
  display: flex;
  align-items: center;
  margin: 0 0rem;
  position: relative;
  padding-top: 2rem;
  overflow: hidden;
  /*grid-template-columns: auto-fit;*/
  /*grid-column-gap: 1rem;*/
}

.story-wrapper{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
}


.story-wrapper h2 {
    margin: .25em;
    font-size: 1.1em;
    font-weight: 400;
}

.story-inner {
    width: 75%;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

 .story-inner:after {
    content: "";
    display: block;
    padding-bottom: 100%;
  }

  .story-img {
    width: calc(100% - .35em);
    height: calc(100% - .35em);
    position: absolute;
    object-fit: cover;
    border-radius: 50%;
    border: 3px solid var(--grey);
    z-index: 2;
  }

   .gradient {
    width: 100%;
    height: 100%;
    padding: .3em;
    position: absolute;
    border-radius: 50%;
    background: linear-gradient(45deg, rgba(214,41,118,1) 0%, rgba(250,126,30,1) 100%);
  }

/*=== Side Content ===*/
.sideContent{
  display: none;
  position: sticky;
  top: 7rem;
  width: 35rem;
  padding: 1rem;
  height: 100%;
  overflow: hidden;
  margin-left: 1rem;
}

.topSideContent{
  display: flex;
  align-items: center;
  justify-content: space-between;
    
}

.topSideContent__middle h3{
  font-size: 1.3rem;
  font-weight: 500;
  cursor: pointer;
} 

.topSideContent__middle span{
  font-size: 1.3rem;
  color: var(--siide-content-grey-color);
}

.change-perfil-link{
  color: var(--blue-link);
  font-weight: 700;
  font-size: 1.1rem;
}

.sideContent__body__top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 2rem 0;
}

.sideContent__body__top span{
  font-size: 1.3rem;
  color: var(--siide-content-grey-color);
  font-weight: 600;
}

.showAll{
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--black);
}

.img-perfil{
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  cursor: pointer;
}

.item-story{
  margin: 0;
  padding: 0;
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.img-perfil-users{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.img-perfil-storie{
  width: calc(100% - .3rem);
  height: calc(100% - .3rem);
  
  object-fit: cover;
  border-radius: 50%;
  border: .2rem solid var(--grey);
  z-index: 2;
}

.gradient-effect {
    width: 100%;
    height: 100%;
    padding: .3em;
    position: absolute;
    border-radius: 50%;
    background: linear-gradient(45deg, rgba(214,41,118,1) 0%, rgba(250,126,30,1) 100%);
}



.sideContent__body{
  margin-top: 2rem;
}

.sideContent__body__body .item{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
}

.sideContent__body__body .item .item__left{
  display: flex;
  align-items: center;
}

.sideContent__body__body .item .item__left > div:nth-child(2){
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}

.sideContent__body__body .item .item__left > div .perfil-users-name{
  font-weight: 500;
  font-size: 1.3rem;
  color: var(--black);
}
.sideContent__body__body .item .item__left > div span{
  color: var(--siide-content-grey-color);
  font-size: 1.1rem;
}

.follow-user{
  color: var(--blue-link);
  font-size: 1.2rem;
  font-weight: 600;
}

.sideContent__footer{
  margin-top: 2rem;

}

.sideContent__footer a,
.sideContent__footer span{
  font-size: 1.2rem;
  color: #cdcdcd;
}

.sideContent__footer .all-link > *{
    margin-right: .5rem;
    cursor: pointer;
}

.sideContent__footer .all-link{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 1.8rem;
}

.copyright{
  
}

/*===  Card Content  ===*/
.card-content{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  background-color: var(--white);
  margin: 2rem 0 6rem;
  border: .1rem solid var(--light-grey);
  border-radius: .3rem;
}

.card-content__top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
}

.card-content__top__left{
  display: flex;
  align-items: center;
}

.card-content__top__left .right-content-info{
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}

.card-content__top__left img{
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
}

.right-content-info .user-name{
  font-size: 1.4rem;
  color: var(--black);
  font-weight: 600;
}

.right-content-info .user-name:hover{
  text-decoration: underline;
}

.right-content-info .user-info-h{
  color: var(--black);
}

.toggle-modal-info{
  font-size: 2rem;
  cursor: pointer;
}

.card-content-img{
  width: 100%;
}

.card-content-img img{
  width: 100%;
  height: 100%;
}

.card-content__footer{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction:column;
  width: 100%;
}

.card-content__footer-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
}

.card-content__footer-body{
  width: 100%;
  padding: .1rem 1rem;
  display: flex;
  flex-direction: column;
}



.card-content__footer-body span{
  font-size: 1.2rem;
  font-weight: 500;
  margin:  0 0 .4rem;
}

.card-content__footer-body span:nth-child(1){
  font-size: 1.3rem;
}

.card-content__footer-body .expand-content, 
.card-content__footer-body .published-day{
  font-weight: 300;
}

.card-content__footer-body .expand-content .expand-content-text,
.card-content__footer-body .published-day{
  color: #8e8e8e;
}

.card-content__footer-body .published-day{
  font-weight: 400;
}

.card-content__footer-body .published-day{
  font-size: 1rem;
}

.card-content__footer-top__left span{
  margin-right: 1rem;
}

.card-content__footer-top span{
  font-size: 2.4rem;
  cursor: pointer;
}

.card-content__footer-footer{
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 1rem;

}

.card-content__footer-footer .emoji-container{
  flex: 1 1 5%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 2.5rem;
}

.card-content__footer-footer .comment-content{
  flex: 1 1 85%;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: 0;
  resize: none;
  overflow: hidden;
  /*overflow-y: scroll;*/

}

.card-content__footer-footer .btn-send-comment{
  flex: 1 1 10%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 1.35rem;
  font-weight: 700;
  color: var(--blue-link);
  cursor: pointer;

}

/*===  Content User  ===*/
.contentUser{
   margin: 8rem 0 0 0;
}

.user-header-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-header-inner{
  width: 80%;
  min-height: 15vh;
  display: grid;
  align-items: center;
  grid-template-columns: auto 1fr;
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;
}

.uh-left {
    width: 100%;
    height: 100%;
    padding: 0 2em 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.uh-image {
    width: 15.5rem;
    height: 15.5rem;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.uh-image-inner {
    width: 14.6rem;
    height: 14.6rem;
    border: .3em solid var(--grey);
    object-fit: cover;
    border-radius: 50%;
    z-index: 2;
}

.uh-right {
    padding: 1em;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
}

.user-info {
    font-size: 2rem;
    line-height: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.user-info .user-info-name-badget{
  display: flex;
  align-items: center;
  justify-content: center;
}

.user-info-name-badget > span{
  font-size: 2.5rem;
  font-weight: 300;
}

.user-info-name-badget > i{
  margin-left: .5rem;
  color: var(--blue);
}

.uname-verified {
    height: 1.8rem;
}

.btn {
    margin: 1em;
    border-radius: 3px;
    /* border: 1px solid var(--grey); */
    background-color: var(--light);
    padding: .4em .6em;
    cursor: pointer;
    color: var(--black);
    border: .1rem solid var(--light-grey);
}

.user-links a {
    margin: 0 1em 0 0;
    cursor: pointer;
    font-size: 1.4rem;
    color: var(--black);
  }
  .user-links a span {
    color: var(--black);
    font-weight: bolder;
  }
  .user-bio{
    font-size: 1.4rem;
  }
  .user-bio-name {
    font-weight: bold;
    margin: 1.75rem 0 1rem 0;
    line-height: 1;
  }

  .user-stories{
    margin: 3em 0 1em 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .user-stories-inner {
    width: 80%;
    /*display: grid;
    grid-column-gap: 4rem;
    grid-template-columns: auto;*/
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

.user-page-tab-navigation{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.user-page-tab-navigation-btn{
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: .1rem solid var(--light-grey);
  width: 80%;
  position: relative;
  padding-top: 1.3rem;
}

.user-page-tab-navigation-btn a{
  margin: 0 3rem;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--black);
  font-weight: 500;
}

.user-page-tab-navigation-btn a i{
  margin-right: .5rem;
  font-size: 1.3rem;
}

.user-page-tab-navigation-btn a:nth-child(n+2){
  opacity: 0.5;
}


  .user-page-wrapper {
    display: flex;
    justify-content: center;
    margin: 3em 0;
  }

  .user-page-inner{
    width: 80%;
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 2rem;
    grid-row-gap: 2rem;
  }

  .image-wrapper{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  .image-wrapper:after {
    content: "";
    display: block;
    padding-bottom: 100%;
  }

  .image {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
  }

  .img-overlay-wrapper {
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    transition: opacity .15s ease-in-out;
  }

  .img-btns {
    font-size: 1.3rem;
    color: var(--white);
    z-index: 10;
  }

  .img-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0, 0, 0, .4);
    z-index: 9;
  }

  .image-block {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 20;
  }
  
  .block {
    width: 60%;
    height: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--light-mode-brig);
    z-index: 20;
  }

  .block-background {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0, 0, 0, .4);
    z-index: 19;
  }

  .user-loader {
    min-height: 25vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .loader {
    position: relative;
    width: 75px;
  }

  .circular {
    animation: rotate 2s linear infinite;
    height: 100%;
    width: 100%;
  }

  .path {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
    stroke-linecap: round;
  }

  /* Keyframes */
  @keyframes rotate {
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes dash {
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -35px;
    }
    100% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -124px;
    }
  }
  @keyframes color {
    100%, 0% {
      stroke: var(--red);
    }
    40% {
      stroke: var(--blue);
    }
    66% {
      stroke: var(--green);
    }
    80%, 90% {
      stroke: var(--yellow);
    }
  }

  /*===  React ELASTIC CAROUSEL ===*/
.rec-carousel-wrapper{
  padding: 0;
  margin: 0;
  width: 100%;
  min-width: 100%;
  max-width: 100%;
}
.rec-carousel{
  position: relative;
  margin: 0;
  padding: 0;
  
  width: 100%;
  min-width: 100%;
  max-width: 100%;
}
.rec-carousel-slider,
.rec-carousel-slider-container{
  width: 100%;
  min-width: 100%;
  max-width: 100%;
}
button.rec-dot{
  background-color: rgb(235, 16, 16);
  box-shadow: 0 0 1px 3px rgba(235, 16, 16, 0.5);
  display: none;
}

button.rec-dot:hover, button.rec-dot:active, button.rec-dot:focus  {
  box-shadow: 0 0 1px 3px rgba(235,16,16,0.5);
  display: none;
}

.rec.rec-arrow {
    position: absolute;
    z-index: 2;
    background-color: var(--white) !important;
    color: var(--black) !important;
    width: 3rem !important;
    height: 3rem;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;  
    min-width: 3rem;
    
}

.rec.rec-arrow:after,.rec.rec-arrow:before{
  content: "";
  width: 3rem !important;
  height: 3rem;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.rec.rec-arrow:hover:enabled, 
.rec.rec-arow:focus:enabled,
.rec.rec-arow:focus,
.rec.rec-arow:active,
.rec.rec-arow:enabled {
    color: var(--black) !important;
    background-color: var(--white) !important;
    box-shadow: 0 0 2px 0 #333;
}



.rec.rec-arrow-left{
  z-index: 5;
  left: .5rem;
}

.rec.rec-arrow-right{
  right: .5rem;
  color: var(--black);
}

.rec.rec-arrow:disabled {
    visibility: hidden;
    cursor: none;
}

